<script lang="coffeescript">


  import { createEventDispatcher } from 'svelte'
  import { slide } from 'svelte/transition'


  dispatch = createEventDispatcher()

  export disabled = false
  export id = uuid()
  export items = []
  export width = '200px'

  export top = undefined

  export selectedIndex = -1
  export size = undefined


  export value = ""
  export blankSlate = ""

  export labelKey = "label"
  export idKey = "id"


  export showMenu = false

  export align = "left"

  # trigger is required
  export trigger = undefined

  # if a trigger is defined, it maps the width to the parent
  # and is placed just below it, used for combobox and custom select
  export matchTrigger = true


  style = undefined

  menuRef = undefined

  hasfocus = false

  highlightedIndex = -1

  changeIndex = (direction) ->
    index = highlightedIndex + direction;

    if (index < 0)
      index = items.length - 1
    else if (index >= items.length)
      index = 0
    highlightedIndex = index


  hide = ()->
    showMenu = false

  debouncedHide = hide.debounce(1500)

  menuId = uuid()



  checkTrigger = (i)->
    #console.log trigger

    #console.log "checktrigger #{align}"

    if i?
      #console.log i
      #console.log trigger
      #console.log i.offsetLeft
      #console.log i.offsetTop

      if matchTrigger
        style = "width: #{width}; top: #{i.offsetTop + i.offsetHeight}px; left: #{i.offsetLeft + 4}px;"
      else
        #console.log "no match trigger"
        if align == "left"
          #console.log trigger.offsetParent
          #console.log trigger.offsetParent.clientHeight
          style = "width: #{width};top: #{trigger.offsetParent.clientHeight + 4}px;"
          #console.log style
        else
          #console.log "align-right"
          #triggerContainerWidth = trigger.offsetParent.clientWidth
          #triggerCoords = trigger.getBoundingClientRect()
          #right = triggerContainerWidth  - (triggerCoords.width)

          topPosition = top || (i.offsetTop + i.offsetHeight)

          style = "width: #{width};top: #{topPosition}px; right: #{i.offsetLeft + i.offsetWidth}px"
      #console.log "offset parent"
      #console.log i.offsetParent
    #console.log style

  `$: checkTrigger(trigger)`

  menuItemClick = (item, index)->
    `showMenu = false`
    dispatch "click", item


  overlayClick = ()->
    `showMenu = false`

  menumouseover = (e)->
    debouncedHide.cancel()

</script>



<div type="overlay" clear open={showMenu} on:click={overlayClick} on:mouseover={debouncedHide}/>
{#if showMenu}
  <div bind:this={menuRef} menu id={menuId} transition:slide="{{duration: 75}}" {style} {align} on:mouseover={menumouseover} >
    <slot></slot>
  </div>
{/if}
<style lang="sass">


  div[menu]
    position: absolute

    background: white
    display: inline-block
    border: 1px solid var(--menu-border)
    width: auto
    text-align: left
    z-index: var(--z-index-menu)
    &[align="left"]
      left: 0


  [menu-item]
    display: block
    position: relative
    width: 100%
    padding: var(--fine-spacing-s) var(--fine-spacing-m)
    &:hover
      background: var(--lighter-gray)



</style>
