<script lang="coffeescript">

  import {onMount} from "svelte"
  import tippy from 'tippy.js'

  export content = undefined

  element = undefined

  tooltip = (element)->
    tippy(element, { content: content, allowHTML: true })
    return {
      destroy: () ->
        console.log "destroyed"
    }

</script>
<style global lang="sass">



</style>



<span data-tippy-content="Tooltip">
  <div use:tooltip><slot/></div>
</span>
