<script lang="coffeescript">
  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()

  import Menu from '../../../components/Overlay/Menu/ComposedMenu.svelte'
  import DeleteConfirmation from '../../../components/Overlay/Modal/DeleteConfirmation.svelte'

  export item = undefined

  menuButtonRef = undefined
  showMenu = false

  openMenu = () ->
    showMenu = true
  closeMenu = ()->
    showMenu = false

  width = "189px"

  destroy = ()->
    dispatch "destroy", item
    closeMenu()

  indent = (indentLevel)->
    console.log "indent"
    dispatch "update", {id: item.id, indent: indentLevel }
    closeMenu()


  showDeleteConfirmation = false

  deleteConfirmation = ()->
    closeMenu()
    showDeleteConfirmation = true

  deleteConfirmationCancel = ()->
    showDeleteConfirmation = false

  destroy = ()->
    dispatch "destroy", item
    showDeleteConfirmation = false

</script>
<style lang="sass">
  ul
    width: 100%
    hr
      border: none
      border-bottom: 1px solid var(--lightest-gray)
      margin-top: 3px
    li
      min-height: 32px
      div
        padding: 6px 12px
      button
        display: inline
        border-radius: 0px
        width: 100%
        background: transparent
        text-align: left
        &[indent]
          &:first-of-type
            margin-left: 2px
          border-radius: 4px
          width: auto
          font-weight: bold
        &[disabled]
          color: var(--light-gray)
        &:hover
          background: var(--lightest-gray)

</style>
<button type="button" icon bind:this={menuButtonRef} on:click={openMenu}>
  <svg class="menu" xmlns="http://www.w3.org/2000/svg" width="24" height="24" version="1.1" viewBox="0 0 24 24"><path d="M12,16A2,2 0 0,1 14,18A2,2 0 0,1 12,20A2,2 0 0,1 10,18A2,2 0 0,1 12,16M12,10A2,2 0 0,1 14,12A2,2 0 0,1 12,14A2,2 0 0,1 10,12A2,2 0 0,1 12,10M12,4A2,2 0 0,1 14,6A2,2 0 0,1 12,8A2,2 0 0,1 10,6A2,2 0 0,1 12,4Z"/></svg>
</button>
<Menu trigger={menuButtonRef} bind:showMenu={showMenu} align="right" {width}>
  <ul>

    <li>
      <button type="button" on:click={deleteConfirmation}>Delete Item</button>
    </li>
  </ul>
</Menu>
{#if showDeleteConfirmation}
  <DeleteConfirmation on:destroy={destroy} on:cancel={deleteConfirmationCancel}/>
{/if}
