<script lang="coffeescript">
  import { createEventDispatcher, onMount } from "svelte"
  dispatch = createEventDispatcher()

  export item = undefined

  # If list item, show an icon when there is no value
  # If not a list item, show text instead
  export isListItem = true

  # Show time entry quick add
  # Only show on items that support time entries
  # tasks, assignments, documents
  export showTimeForm = false


  newValue = undefined

  import Popover from '../../../components/Overlay/Popover/Popover.svelte'
  import Menu from "../../../components/Overlay/Menu/ComposedMenuFavorites.svelte"

  import EffortFormat from '../../../components/Format/EffortFormat.svelte'
  import DurationInput from "../../../components/Form/TranslatedInput/DurationInput.svelte"
  # menu

  show = false
  stub = {
    estimate: 0
  }


  effort = 0

  onMount ()->

    effort = item.estimate / 60

  triggerClick = ()->
    console.log "trigger click"
    dispatch "open"
    show = true


  triggerRef = undefined
  progressMenuClick = (e)->
    console.log "progressmenuclick"

  durationKeyup = (e)->
    if (e.key == "Escape")
      cancel()


  cancel = ()->
    show = false
    dispatch "close"


  save = ()->
    # show = false
    progressItem = { id: item.id, estimate: stub.estimate }


    dispatch "close"
    dispatch "update", progressItem


  add = ()->
    # show = false
    progressItem = { estimated_hours: newValue }


    dispatch "close"
    dispatch "save", progressItem




  newTime = 0



  updateEffortMinutes = (e)->
    stub.estimate = e.detail.inMinutes





</script>

<div>

  <div on:click={triggerClick} bind:this={triggerRef}>
      {#if (item.estimate || 0) == 0}
        {#if isListItem}
          <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
            <path fill="none" d="M0 0h24v24H0V0z"/>
            <path d="M11.99 2C6.47 2 2 6.48 2 12s4.47 10 9.99 10C17.52 22 22 17.52 22 12S17.52 2 11.99 2zM12 20c-4.42 0-8-3.58-8-8s3.58-8 8-8 8 3.58 8 8-3.58 8-8 8zm.5-13H11v6l5.25 3.15.75-1.23-4.5-2.67z" class="fill"/>
          </svg>
        {:else}
          <a duedate {isListItem} style="color: var(--gray)"><em>No estimate</em></a>
        {/if}

      {:else}
        <a duedate style="color: var(--gray)"><em>
          {#if !isListItem}
            <span duedate>Estimate: </span>
          {/if}
          <EffortFormat bind:value={item.estimate}/>
        </em></a>

      {/if}


  </div>
  <Menu bind:showMenu={show} bind:trigger={triggerRef} matchTrigger={false} align="right" top="24px">

    <ul>
      <li label>Estimate</li>
      <li><DurationInput bind:value={effort} on:change={updateEffortMinutes} autofocus on:keyup={durationKeyup}/></li>
      <li>
        <div layout="row">
          <div flex="flex"/>
          <div flex="initial">
            <button type="button" on:click={cancel}>
              <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path fill="none" d="M0 0h24v24H0V0z"/><path d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12 19 6.41z"/></svg>
            </button>
          </div>
          <div flex="initial">
            <button type="button" on:click={save}>
              <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path fill="none" d="M0 0h24v24H0V0z"/><path d="M9 16.17L4.83 12l-1.42 1.41L9 19 21 7l-1.41-1.41L9 16.17z"/></svg>
            </button>
          </div>
          <div flex="initial">
          </div>
        </div>
      </li>

      {#if showTimeForm}
        <hr/>
        <li label>Quick Time Entry</li>
        <li>

        </li>


      {/if}

    </ul>



  </Menu>
</div>

<style lang="sass">

  ul
    width: 100%
    hr
      border: none
      border-bottom: 1px solid var(--lightest-gray)
    li
      min-height: 32px
      min-width: 200px
      &[label]
        padding: 6px 12px
      [effort]
        margin-left: 8px
      div
        padding: 4px 4px
      button
        display: inline
        border-radius: 0px
        width: 100%
        background: transparent
        text-align: left
        &[indent]
          &:first-of-type
            margin-left: 2px
          border-radius: 4px
          width: auto
          font-weight: bold
        &[disabled]
          color: var(--light-gray)
        &:hover
          background: var(--lightest-gray)

