<script lang="coffeescript">
  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()

  import {push} from 'svelte-spa-router'


  import Menu from "../../../components/Overlay/Menu/ComposedMenuFavorites.svelte"
  import Project from "../../../helpers/project.js"

  resource = new Project()
  list = []
  getProjects = ()->
    scb = (projects)->
      list = projects
    resource.select(scb, null, null)

  getProjects()

  export item = undefined


  menuButtonRef = undefined
  showMenu = false

  goToCollections = ()->
    push "#/projects"

  goToProject = (item)->
    showMenu = false
    push "#/projects/#{item.id}"

  openMenu = () ->
    showMenu = true
  closeMenu = ()->
    showMenu = false

  debouncedMenu = openMenu.debounce(500)

  width = "200px"

  sort = (list)->
    if list?
      return list.sortBy "ordinal"
    else
      []
  `$: sortedlist = sort(list)`
</script>
<style lang="sass">
  span
    font-weight: inherit
  ul
    width: 100%
    hr
      border: none
      border-bottom: 1px solid var(--lightest-gray)
    li
      min-height: 32px
      min-width: 200px
      div
        padding: 6px 12px
      button
        display: inline
        border-radius: 0px
        width: 100%
        background: transparent
        text-align: left
        &[indent]
          &:first-of-type
            margin-left: 2px
          border-radius: 4px
          width: auto
          font-weight: bold
        &[disabled]
          color: var(--light-gray)
        &:hover
          background: var(--lightest-gray)

</style>
<span>
<a href="#/projects"  bind:this={menuButtonRef} on:mouseover={debouncedMenu}><em>Collections</em></a>
<Menu trigger={menuButtonRef} bind:showMenu={showMenu} align="left" {width} matchTrigger={true}>
  <ul>

    <li>
      <button type="button" on:click={goToCollections}>Collections</button>
    </li>

    {#if list}
    <hr/>
    {#each sortedlist as item, index}
    <li>
      <button type="button" on:click={goToProject(item)} ellipsis><span ellipsis>{item.name}</span></button>
    </li>
    {/each}
    {/if}
  </ul>
</Menu>
</span>
