<script lang="coffeescript">

  import { createEventDispatcher, afterUpdate } from 'svelte'
  dispatch = createEventDispatcher()

  import Calendar from "./Calendar.svelte"
  import Popover from '../../Overlay/Popover/Popover.svelte'

  export value = Date.create()
  export direction = 'bottom'
  export flipped = false

  export id = uuid()

  export open = false
  export style = undefined




  open = false


  buttonRef = undefined
  buttonWidth = undefined
  tooltipRef = undefined
  triggerRef = undefined

  openMenu = () ->
    open = true


  closeMenu = () ->
    open = false


  toggleMenu = () ->
    open = !open


  onKeydown = (event) ->
    if (event.key == 'Escape')
      event.stopPropagation()
      open = false


  selectedDate = value.format("{yyyy}-{MM}-{dd}")

  setDay = (e) ->

    value = e.detail
    selectedDate = e.detail.format("{yyyy}-{MM}-{dd}")
    dispatch("change", selectedDate)
    open = false



</script>
<style lang="sass">

  [role=tooltip]
    box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.3)
    //display: none

    flex-direction: column
    align-items: flex-start
    position: absolute
    z-index: 10000
    background-color: #ffffff
    width: auto
    list-style: none
    top: 32px
    left: 0
    border-radius: 6px


    text-align: left
    font-size: 12px
    line-height: 18px




    &[data-floating-menu-direction='bottom']::after
      top: -0.1875rem
      left: 0
      width: 2rem
      height: 0.1875rem

    &[data-floating-menu-direction='top']::after
      bottom: -0.5rem
      left: 0
      width: 2rem
      height: 0.5rem

    &[data-floating-menu-direction='left']::after
      right: -0.375rem
      top: 0
      height: 2rem
      width: 0.375rem

    &[data-floating-menu-direction='right']::after
      top: 0
      left: -0.375rem
      height: 2rem
      width: 0.375rem


  [tooltipcontainer]
    border: 1px solid black
    position: relative
</style>


<div layout="row">
  <div flex="initial" style="width:100px">
    <input type="text" bind:value={selectedDate} on:keyup/>
  </div>
  <div flex="initial" style="width: 40px">
    <button type="button" icon bind:this={triggerRef} on:click={openMenu}>
      <svg xmlns="http://www.w3.org/2000/svg" height="24" viewBox="0 0 24 24" width="24"><path d="M0 0h24v24H0V0z" fill="none"/><path d="M22 3h-3V1h-2v2H7V1H5v2H2v20h20V3zm-2 18H4V8h16v13z"/></svg>
    </button>
  </div>
  <Popover bind:show={open} bind:trigger={triggerRef} width="240px" align="left">
      <Calendar bind:value={selectedDate} on:change={setDay}/>
  </Popover>
</div>
