<script lang="coffeescript">
  import { createEventDispatcher} from "svelte"
  dispatch = createEventDispatcher()

  export item = undefined
  export value = undefined

  newValue = undefined



  import Menu from "../../../components/Overlay/Menu/ComposedMenuFavorites.svelte"
  import Tooltip from '../../../components/Overlay/Tooltip/Tooltip.svelte'






  export project = undefined

  # menu

  show = false
  triggerRef = undefined

  triggerClick = ()->
    dispatch "open"
    show = true




  save = (change)->
    dispatch "update", change

  debouncedSave = save.debounce(500)





  assignmentClick = (assignment)->
    unless item.assigned_to?
      item.assigned_to = []

    if item.assigned_to.includes(assignment.id)
      # remove it
      item.assigned_to.remove(assignment.id)
    else
      item.assigned_to.push assignment.id
    item.assigned_to = item.assigned_to.unique()
    debouncedSave { id: item.id, assigned_to: item.assigned_to }

  toggleAll = ()->
    # clear it
    if sortedList.length == (item.assigned_to || []).length
      item.assigned_to = []
    else
      item.assigned_to = sortedList.map (o)->
        return o.id
    debouncedSave { id: item.id, assigned_to: item.assigned_to }

  sort = (val)->
    if val?
      return val.sortBy "ordinal"
    else
      []

  match = (assigned_ids)->

    output = sort(project.assignments).filter (a)->
      return (assigned_ids || []).includes(a.id)
    output

  getTooltip = (v)->
    if (v.assigned_to || []).length == 0
      return "Unassigned"
    else
      options =  match(v.assigned_to).map (i)->
        return "<li>#{i.name}</li>"

      return "<ul>#{options}</ul>"




  `$: assigned = match(item.assigned_to)`
  `$: sortedList = sort(project.assignments)`
  `$: tooltip = getTooltip(item)`

</script>
<style lang="sass">



  span
    font-weight: inherit
  [icon]
  ul
    width: 100%
    hr
      border: none
      border-bottom: 1px solid var(--lightest-gray)
    li
      min-height: 32px
      min-width: 200px
      div
        padding: 4px 4px
      button
        display: inline
        border-radius: 0px
        width: 100%
        background: transparent
        text-align: left
        &[indent]
          &:first-of-type
            margin-left: 2px
          border-radius: 4px
          width: auto
          font-weight: bold
        &[disabled]
          color: var(--light-gray)
        &:hover
          background: var(--lightest-gray)

</style>

<div>
  <div on:click={triggerClick} bind:this={triggerRef}>
    <Tooltip content={tooltip}>

      <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" version="1.1" viewBox="0 0 24 24">
        {#if (item.assigned_to || []).length == 0}
          <path class="fill" d="M19,3H14.82C14.4,1.84 13.3,1 12,1C10.7,1 9.6,1.84 9.18,3H5A2,2 0 0,0 3,5V19A2,2 0 0,0 5,21H19A2,2 0 0,0 21,19V5A2,2 0 0,0 19,3M12,3A1,1 0 0,1 13,4A1,1 0 0,1 12,5A1,1 0 0,1 11,4A1,1 0 0,1 12,3M7,7H17V5H19V19H5V5H7V7Z"/>
        {:else}
          <path class="fill" d="M19,3A2,2 0 0,1 21,5V19A2,2 0 0,1 19,21H5A2,2 0 0,1 3,19V5A2,2 0 0,1 5,3H9.18C9.6,1.84 10.7,1 12,1C13.3,1 14.4,1.84 14.82,3H19M12,3A1,1 0 0,0 11,4A1,1 0 0,0 12,5A1,1 0 0,0 13,4A1,1 0 0,0 12,3M7,7V5H5V19H19V5H17V7H7M12,9A2,2 0 0,1 14,11A2,2 0 0,1 12,13A2,2 0 0,1 10,11A2,2 0 0,1 12,9M8,17V16C8,14.9 9.79,14 12,14C14.21,14 16,14.9 16,16V17H8Z"/>
        {/if}
      </svg>
    </Tooltip>
  </div>
  <Menu bind:showMenu={show} bind:trigger={triggerRef} matchTrigger={false} align="right" top="24px">
    <ul>
      <li>
        <button type="button" on:click={toggleAll} ellipsis>
          <div layout="row">
            <div flex="initial" style="width: 24px; height: 24px;">
              <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" version="1.1" viewBox="0 0 24 24"></svg>
              </div>
              <div flex="flex" ellipsis style="padding-left: 4px">Toggle All</div>
          </div>
        </button>
      </li>
      <hr/>
      {#each sortedList as assignment}
        <li>
          <button type="button" on:click={assignmentClick(assignment)}>
            <div layout="row">

              <div flex="initial" style="width: 24px; height: 24px;">
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" version="1.1" viewBox="0 0 24 24">
                  {#if (item.assigned_to || []).includes(assignment.id)}
                    <path fill="none" d="M0 0h24v24H0V0z"/><path d="M9 16.2L4.8 12l-1.4 1.4L9 19 21 7l-1.4-1.4L9 16.2z" class="fill"/>
                  {/if}
                </svg>
              </div>
              <div flex="flex" ellipsis style="padding-left: 4px">{assignment.name}</div>
            </div>
          </button>
        </li>
      {/each}

    </ul>
  </Menu>
</div>
